@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap");
* {
  box-sizing: border-box; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0; }

body {
  font-family: 'Playfair Display', serif;
  font-weight: normal;
  background: #E4E4E4; }

.container {
  max-width: 1140px;
  margin: 0 auto; }
  @media (max-width: 1200px) {
    .container {
      max-width: 980px;
      margin: 0 auto; } }
  @media (max-width: 768px) {
    .container {
      max-width: 86%;
      margin: 0 auto; } }

.photo {
  transition: opacity 0.4s ease; }
  .photo-head {
    padding: 100px 0 50px;
    position: fixed;
    top: 0;
    left: 50%;
    width: 100%;
    transform: translate(-50%);
    background: #E4E4E4;
    text-align: center;
    z-index: 1000; }
  .photo-head.unfixed {
    position: absolute; }
  .photo-head.unfixed .photo-desc {
    display: block; }
  .photo-head.unfixed .photo-subtitle {
    display: none; }
  .photo-title {
    font-size: 60px;
    line-height: 1.13em;
    margin-bottom: 0.25em; }
  .photo-subtitle {
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 300;
    font-size: 24px;
    line-height: 1.37em;
    display: none; }
  .photo-desc {
    font-size: 20px;
    line-height: 1.5em;
    letter-spacing: 0.0185714em;
    max-width: 530px;
    margin: 0 auto; }
  .photo-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.3);
    transition: transform 0.4s; }
    .photo-modal__image {
      position: absolute;
      top: 50%;
      left: 50%;
      max-width: 95%;
      max-height: 95%;
      width: auto;
      height: auto;
      transform: translate(-50%, -50%);
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.4); }
  .photo-modal::after {
    content: 'Кликните для закрытия';
    display: block;
    font-size: 12px;
    letter-spacing: 0.08em;
    padding: 0.25em;
    border-radius: 0.25em;
    background: #E4E4E4;
    position: absolute;
    bottom: 0.5em;
    left: 50%;
    transform: translateX(-50%); }

.gallery {
  display: block;
  padding-bottom: 120px;
  position: relative; }
  .gallery-inner {
    margin: -30px -15px;
    display: flex;
    justify-content: center; }
  .gallery-column {
    max-width: 390px;
    width: 100%; }
  .gallery-item {
    cursor: pointer;
    margin: 30px 15px; }
    .gallery-item_xs {
      height: 188px; }
    .gallery-item_s {
      height: 240px; }
    .gallery-item_m {
      height: 350px; }
    .gallery-item_l {
      height: 540px; }
    .gallery-item img {
      width: 100%;
      height: 100%;
      object-fit: cover; }

.gallery::after {
  content: url("../../loading.svg");
  display: none;
  width: 100px;
  height: 100px;
  object-fit: contain;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  opacity: 0.5; }

.gallery.loading::after {
  display: block; }

/* *********** MEDIA ************ */
@media (max-width: 1200px) {
  .photo-head {
    padding: 70px 0 30px; }
  .gallery-inner {
    margin: -20px -10px; }
  .gallery-item {
    margin: 20px 10px; } }

@media (max-width: 768px) {
  .photo-head {
    padding: 40px 0 20px; }
  .photo-title {
    font-size: 40px;
    margin-bottom: 0; }
  .photo-subtitle {
    display: block; }
  .photo-desc {
    display: none;
    font-size: 20px;
    max-width: 100%; }
  .gallery-inner {
    margin: -5px -2.5px; }
  .gallery-item {
    margin: 5px 2.5px; }
    .gallery-item_xs {
      height: 150px; }
    .gallery-item_s {
      height: 192px; }
    .gallery-item_m {
      height: 280px; }
    .gallery-item_l {
      height: 432px; }
    .gallery-item img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }

@media (max-width: 600px) {
  .photo-head {
    padding: 30px; }
  .photo-title {
    font-size: 40px; }
  .photo-desc {
    font-size: 20px;
    max-width: 100%; }
  .gallery-inner {
    flex-wrap: wrap;
    margin: -20px 0; }
  .gallery-column {
    max-width: 600px;
    width: 100%; }
  .gallery-item {
    margin: 20px 0; }
    .gallery-item_xs {
      height: 188px; }
    .gallery-item_s {
      height: 240px; }
    .gallery-item_m {
      height: 350px; }
    .gallery-item_l {
      height: 540px; }
    .gallery-item img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }

/* *********** MEDIA ************ */
/* *********** LIBRARY ************ */
.b-lazy {
  -webkit-transition: opacity 500ms ease-in-out;
  -moz-transition: opacity 500ms ease-in-out;
  -o-transition: opacity 500ms ease-in-out;
  transition: opacity 500ms ease-in-out;
  max-width: 100%;
  opacity: 0; }

.b-lazy.b-loaded {
  opacity: 1; }

@-webkit-keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

.animate__fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  animation-duration: 2s; }

@-webkit-keyframes fadeInDown_m {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInDown_m {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-50%, -100%, 0);
    transform: translate3d(-50%, -100%, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0); } }

.animate__fadeInDown_m {
  -webkit-animation-name: fadeInDown_m;
  animation-name: fadeInDown_m;
  animation-duration: 0.8s; }

